<template>
    <div class="page">
        <van-nav-bar
                title="个人中心"
                fixed
        />
        <section class="noLogin" v-if="!userInfo.id">
            <img :src="noLogin" alt="">
            <span class="prelogin-text">登录后查看更多信息</span>
            <van-button @click="$router.push('/user/login')" style="width: 16rem;margin-top: 3rem;" round type="info">立即登录</van-button>
        </section>
        <section v-else class="mine-box">
            <div class="top">
                <div class="left">
                    <Avatar style="transform: scale(2)" :src="userInfo.avatar" icon="ios-person" size="large" />
                </div>
                <div class="right">
                    <div class="username">{{userInfo.username || userInfo.phone}}</div>

                </div>
            </div>
          <div class="middle">
            <van-grid clickable >
              <van-grid-item to="/user/myCollect" icon="star-o" text="收藏" />
              <van-grid-item to="/user/myRecord" icon="orders-o" text="答题记录" />
              <van-grid-item to="/user/question" icon="question-o" text="向管理员提问" />
              <van-grid-item to="/user/setting" icon="setting-o" text="设置" />
            </van-grid>
          </div>
            <div class="bottom">
                <van-button @click="gout" style="width: 100%;">退出登录</van-button>
            </div>
        </section>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    import config from "../../../config";
    import {Dialog} from "vant";

    export default {
        name: "Mine",
        data(){
            return{
                noLogin: 'https://www.it666.com/h5/static/images/noLoginEmpty.png'
            }
        },
        computed:{
            ...mapState(['userInfo'])
        },
        methods:{
            gout(){
                Dialog.confirm({
                    title: '退出登录',
                    message: '确认退出账号吗？',
                    confirmButtonText: '退出'
                }).then(async () => {
                    config.delCache('userId');
                    this.$store.dispatch('addUserInfo', {userInfo: {}});
                    this.$router.replace('/user')
                }).catch(() => {});
            }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
    >>>.van-nav-bar{
        background-color: #1989fa
    }
    >>>.van-nav-bar__title{
        color white
    }
        .mine-box{
            width: 100%
            background: #f9f9f9
            >.bottom{
                width: 100%
                margin-top: 1rem
                background-color: #fff
                padding: 2rem
            }
            >.top{
                width: 100%
                height: 15rem
                background-color: #fff
                display flex
                >.left{
                    flex 3
                    max-width 13rem
                    min-width 13rem
                    height: 100%
                    display flex
                    align-items: center;
                    justify-content center
                }
                >.right{
                    flex 6
                    height: 100%
                    display flex
                    align-items center
                    position relative
                    >.username{
                        font-size: 1.7rem
                    }
                    >.setting{
                        position absolute
                        right: 2rem
                        top: 2rem
                        >svg{
                            width: 2rem
                            height: 2rem
                        }
                    }
                }
            }
        }
    .noLogin{
        width: 100%
        display flex
        flex-direction column
        justify-content center
        align-items center
        background-color: #fff
        padding-bottom: 9rem
        >.prelogin-text{
            color: #b0bdc9;
            font-size: 1.5rem
            margin-top: 1.6rem
        }
        >img{
            margin-top: 15rem
            width: 12rem
        }
    }
</style>
